import { Button, Grid, Typography } from '@material-ui/core';
import React from 'react';
import { Link, useHistory } from 'react-router-dom';
import { ReactComponent as SpotifyLogo } from './Components/LeftNav/Components/spotifylogo.svg';
import './LoginPage.css';

export default function LoginPage(props) {
	let history = useHistory();

	const clientid = '5c5132a7eec2412e846544dc0003d61e';
	const authEndpoint = 'https://accounts.spotify.com/authorize';
	console.log(props.currentL);
	const redirectUri = props.currentL;
	const scopes = ['user-read-currently-playing', 'user-read-recently-played'];
	//this is basically a very very long url nothing more so Im using variables to create the url and compartmentalize it
	const loginUrl = `${authEndpoint}?client_id=${clientid}&scope=${scopes.join(
		'%20'
	)}&response_type=token&show_dialog=true&redirect_uri=${redirectUri}`;

	return (
		<Grid
			item
			className="login-cont"
			container
			style={{
				color: props.colorMode === 'Dark' ? 'white' : 'black',
			}}
			justify="center"
			alignItems="flex-start"
		>
			<Grid item container justify="center" alignItems="center">
				<SpotifyLogo
					style={{ cursor: 'pointer' }}
					className={`spotify-logo ${
						props.colorMode === 'Dark' ? 'slogodark' : 'slogolight'
					}`}
					onClick={() => window.location.reload()}
				/>
			</Grid>

			<Grid
				item
				xs={8}
				container
				className="login-content"
				justify="center"
				direction="column"
				alignContent="center"
				spacing={4}
				style={{
					color: props.colorMode === 'Dark' ? 'white' : 'black',
				}}
			>
				<Grid
					item
					xs={12}
					container
					justify="center"
					className="login-text-cont"
				>
					<Typography className="login-text">
						To continue, please <strong>login</strong> to Spotify by{' '}
						<strong>clicking</strong> the button below
					</Typography>
				</Grid>
				<Grid item xs={12} container justify="center">
					<a href={loginUrl} className="login-button">
						Login with spotify
					</a>
				</Grid>
			</Grid>
		</Grid>
	);
}
